.ss-stage {
  height: 600vh; /* 6 steps scroll */
  background: linear-gradient(180deg, #111, #000);
  position: relative;
  width: 96%;
  border: 1px solid white;
}

.ss-wrapper {
  position: sticky;
  top: 0;
  height: 100vh;
  display: flex;
  overflow: hidden;
  background: #000;
   background-color: var(--bg-color);
}

/* ✅ HEADER */
.ss-header {
  position: absolute;
  top: 50px;
  left: 60px;
  z-index: 10;
}

.ss-header h2 {
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 10px;
}

.ss-header p {
  font-size: 15px;
  opacity: 0.7;
  max-width: 520px;
}

/* ✅ TABLE COLUMNS */
.ss-card {
  flex: 1;
  border-left: 1px solid rgba(255,255,255,0.25);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: flex 0.3s ease;
  overflow: hidden;
}

/* ✅ IMAGE */
.ss-card img {
  width: 90px;
  opacity: 0.6;
  transition: all 0.4s ease;
  margin-left: -20%;
  margin-top: 100%;
}

/* ✅ TEXT */
.ss-card p {
  position: absolute;
  top: 50px;
  text-align: left;
  margin-left: -10%;
  width: 75%;
  font-size: 16px;
  opacity: 1;
  transition: all 0.1s ease;
  font-family: Raleway;
font-weight: 700px;
font-style: Bold;
leading-trim: NONE;
line-height: 100%;
letter-spacing: 0%;
}

/* ✅ STEP NUMBER */
.ss-card span {
  position: absolute;
  bottom: 20px;
  font-size: 50px;
  margin-left:-50%;
  color: rgba(255,255,255,0.4);
}

/* ✅ ACTIVE EXPAND */
.ss-card.active {
  flex: 4;
}

.ss-card.active img {
  width: 200px;
  opacity: 1;
  margin-top: -190px ;
  margin-left: 160px;
}

.ss-card.active p {
  opacity: 1;
  transform: translateY(-10px);
  margin-top: 70%;
  font-size: 25px;
}
.ss-card.active span {
  font-size: 70px;
  margin-top: 0px;
  bottom: 490px;
  margin-left: -70%;
}   
.po {
  display: none;
  color: white;
  font-size: 15px !important;
  /* font-size: 0.5rem; */
  line-height: 1.6;
  text-align: left;
  opacity: 0.85;
  width: 80%;
}

/* ✅ Show only when card is active */
.ss-card.active .po {
  display: block;
  margin-top: 85%;
}
@media (max-width: 768px) {
  .ss-stage {
    width: 100%;
    height: 600vh;
    border: none;
  }

  .ss-wrapper {
    flex-direction: column;       /* ✅ stack cards vertically */
    height: 100vh;
  }

  .ss-card {
    flex: unset;
    width: 100%;
    min-height: 100vh;
    border-left: none;
    border-top: 1px solid rgba(255,255,255,0.2);
  }

  .ss-card.active {
    flex: unset;
  }

  /* ✅ IMAGE */
  .ss-card img {
    width: 70px;
    margin-left: 0;
    margin-top: 20%;
  }

  .ss-card.active img {
    width: 120px;
    margin-left: 0;
    margin-top: 10%;
  }

  /* ✅ MAIN TEXT */
  .ss-card p {
    position: relative;
    top: auto;
    margin-left: 0;
    width: 90%;
    font-size: 14px;
    text-align: center;
  }

  .ss-card.active p {
    font-size: 18px;
    margin-top: 20px;
  }

  /* ✅ STEP NUMBER */
  .ss-card span {
    position: relative;
    bottom: auto;
    margin-left: 0;
    margin-top: 10px;
    font-size: 36px;
  }

  .ss-card.active span {
    font-size: 48px;
    margin-left: 0;
    bottom: auto;
  }

  /* ✅ PO TEXT */
  .po {
    width: 90%;
    font-size: 13px !important;
    text-align: center;
  }

  .ss-card.active .po {
    margin-top: 12px;
  }
}
@media (max-width: 480px) {
  .ss-card img {
    width: 60px;
  }

  .ss-card.active img {
    width: 100px;
  }

  .ss-card p {
    font-size: 13px;
  }

  .ss-card.active p {
    font-size: 16px;
  }

  .ss-card span {
    font-size: 30px;
  }

  .ss-card.active span {
    font-size: 42px;
  }

  .po {
    font-size: 12px !important;
  }
}
